@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;600;700&display=swap);
*{margin:0;padding:0;box-sizing:border-box;font-family:'Nunito', sans-serif}nav{display:flex;justify-content:space-between;align-items:center;padding:1em;color:white;background:#dd3b3b}nav a{text-decoration:none}nav h1{color:white;font-size:36px}nav .nav-links{display:flex}nav .nav-links>*:first-child{margin-right:32px}nav .user-area{display:flex}nav .user-area button{padding:0.25em 1em;font-size:18px;color:black;background:white;border:none;border-radius:8px;cursor:pointer;transition:0.15s all ease-in-out}nav .user-area button:hover{-webkit-filter:brightness(0.9);filter:brightness(0.9)}nav .user-area .logged-in{display:flex;align-items:center;font-size:21px}nav .user-area .logged-in img{display:block;margin-right:8px;border-radius:50%;height:50px;width:auto}nav .user-area .logged-in .email{margin-right:8px}

main{padding:1em;display:grid;grid-template-columns:repeat(auto-fit, minmax(400px, 1fr));grid-gap:1em}main .menu-item img{width:100%;height:auto;border-radius:16px}main .menu-item a{margin-top:0.25em;text-decoration:none;padding:0.5em 2em;background:#dd3b3b;display:inline-block;color:white;border-radius:16px;transition:all 0.1s ease-in-out}main .menu-item a:hover{-webkit-filter:brightness(0.9);filter:brightness(0.9)}

.game-page{padding:1em;display:block}.game-page img{cursor:pointer;display:block;width:100%;height:auto}.modal-content{background:none;min-width:400px}.modal-content button{display:inline-block;padding:4px 16px;border:none;background:#ff5353;color:white;border-radius:8px}.modal-content .block{display:block;padding-top:8px}

.leaderboard{width:30%;margin:auto}.leaderboard .menu .menu-row{display:flex;justify-content:space-between}

